import ApolloClient from 'apollo-boost';
import { GET_TODOS } from './queries';
import { gql } from 'apollo-boost';

const client = new ApolloClient({
  uri: 'https://famous-doe-63.hasura.app/v1/graphql'
});

client.query({
  query: gql`
    query getTodos {
      todos {
          done
          id
          text
      }
  }
  `
})

export default client;
